import React from "react";
import style from "./Paginated.module.css";

export default function Paginated({ recipePage, recipes, paginated, page }) {
    const pages = [];

    for (let i = 1; i <= Math.ceil(recipes / recipePage); i++) {
        pages.push(i);

    }

    const prev = "<";
    const next = ">";

    return (
        <div className={style.bg}>
            <button type="button" onClick={() => paginated(page === 1 ? page : page - 1)}>
                {prev}
            </button>
            <div className={style.btn}>
                {pages?.map((n) => {
                    return (
                        <div key={n}>
                            <button type="buton" onClick={() => paginated(n)}>
                                {page === n ? "*" : n}
                            </button>
                        </div>
                    );
                })}
            </div>
            <button type="button" onClick={() => paginated(page === pages.length ? page : page + 1)}>
                {next}
            </button>
        </div>
    );
}